'use client'

import { useState, useMemo } from 'react'
import { Header } from '@/components/header'
import { Footer } from '@/components/footer'
import { ProductCard } from '@/components/product-card'
import { products } from '@/lib/products'
import { ChevronDown } from 'lucide-react'

type SortOption = 'featured' | 'price-low' | 'price-high' | 'newest' | 'rating'

export default function MenPage() {
  const [selectedSubcategory, setSelectedSubcategory] = useState<string | null>(null)
  const [sortBy, setSortBy] = useState<SortOption>('featured')
  const [season, setSeason] = useState<'All-Season' | 'Winter' | null>(null)

  const subcategories = ['T-Shirts', 'Shirts', 'Pants', 'Jackets']

  const filteredAndSortedProducts = useMemo(() => {
    let filtered = products.filter(p => p.category === 'Men')

    if (selectedSubcategory) {
      filtered = filtered.filter(p => p.subcategory === selectedSubcategory)
    }

    if (season) {
      filtered = filtered.filter(p => p.season === season || p.season === 'All-Season')
    }

    switch (sortBy) {
      case 'price-low':
        filtered.sort((a, b) => a.price - b.price)
        break
      case 'price-high':
        filtered.sort((a, b) => b.price - a.price)
        break
      case 'rating':
        filtered.sort((a, b) => b.rating - a.rating)
        break
      case 'newest':
        filtered.sort((a, b) => b.id - a.id)
        break
      default:
        break
    }

    return filtered
  }, [selectedSubcategory, sortBy, season])

  return (
    <>
      <Header />
      <main className="min-h-screen bg-background">
        {/* Page Header */}
        <div className="bg-muted/50 px-4 md:px-6 py-8">
          <div className="max-w-7xl mx-auto">
            <h1 className="text-3xl md:text-4xl font-bold mb-2">Men's Collection</h1>
            <p className="text-muted-foreground">
              Discover premium menswear for every style and occasion
            </p>
          </div>
        </div>

        <div className="max-w-7xl mx-auto px-4 md:px-6 py-12">
          {/* Filters */}
          <div className="mb-8 space-y-4">
            <div className="flex flex-col md:flex-row gap-4">
              {/* Sub-Category Filter */}
              <div className="flex flex-wrap gap-2">
                <button
                  onClick={() => setSelectedSubcategory(null)}
                  className={`px-4 py-2 rounded-full smooth-transition ${
                    selectedSubcategory === null
                      ? 'bg-primary text-primary-foreground'
                      : 'bg-muted hover:bg-muted/80 text-foreground'
                  }`}
                >
                  All
                </button>
                {subcategories.map((cat) => (
                  <button
                    key={cat}
                    onClick={() => setSelectedSubcategory(cat)}
                    className={`px-4 py-2 rounded-full smooth-transition ${
                      selectedSubcategory === cat
                        ? 'bg-primary text-primary-foreground'
                        : 'bg-muted hover:bg-muted/80 text-foreground'
                    }`}
                  >
                    {cat}
                  </button>
                ))}
              </div>

              {/* Season Filter */}
              <div className="flex gap-2">
                <button
                  onClick={() => setSeason(null)}
                  className={`px-4 py-2 rounded-full smooth-transition ${
                    season === null
                      ? 'bg-primary text-primary-foreground'
                      : 'bg-muted hover:bg-muted/80 text-foreground'
                  }`}
                >
                  All Season
                </button>
                <button
                  onClick={() => setSeason('Winter')}
                  className={`px-4 py-2 rounded-full smooth-transition ${
                    season === 'Winter'
                      ? 'bg-primary text-primary-foreground'
                      : 'bg-muted hover:bg-muted/80 text-foreground'
                  }`}
                >
                  Winter
                </button>
              </div>

              {/* Sort */}
              <div className="md:ml-auto">
                <select
                  value={sortBy}
                  onChange={(e) => setSortBy(e.target.value as SortOption)}
                  className="px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                >
                  <option value="featured">Featured</option>
                  <option value="newest">Newest</option>
                  <option value="price-low">Price: Low to High</option>
                  <option value="price-high">Price: High to Low</option>
                  <option value="rating">Top Rated</option>
                </select>
              </div>
            </div>

            <div className="text-sm text-muted-foreground">
              Showing {filteredAndSortedProducts.length} products
            </div>
          </div>

          {/* Products Grid */}
          {filteredAndSortedProducts.length === 0 ? (
            <div className="text-center py-12">
              <p className="text-lg text-muted-foreground">No products found</p>
            </div>
          ) : (
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
              {filteredAndSortedProducts.map((product) => (
                <ProductCard key={product.id} {...product} />
              ))}
            </div>
          )}
        </div>
      </main>
      <Footer />
    </>
  )
}
